export const [CN, EN, PT, JP] = ['cn', 'en', 'pt', 'jp']

const language = {
  [CN]: {
    titleInfo: '',
    titleWarning: '',
    titleError: '',
    titleSuccess: '',
    titleConfirm: '',
    btnOk: '',
    btnCancel: '',
    maskText: ''
  },
  [EN]: {
    titleInfo: 'Information',
    titleWarning: 'Warning',
    titleError: 'Error',
    titleSuccess: 'Success',
    titleConfirm: 'Confirmation',
    btnOk: 'OK',
    btnCancel: 'Cancel',
    maskText: 'Loading'
  },
  [PT]: {
    titleInfo: 'Aviso',
    titleWarning: 'Alerta',
    titleError: 'Erro',
    titleSuccess: 'Sucesso',
    titleConfirm: 'Confirmao',
    btnOk: 'OK',
    btnCancel: 'Cancelar',
    maskText: 'Carregando'
  },
  [JP]: {
    titleInfo: '',
    titleWarning: '',
    titleError: '',
    titleSuccess: '',
    titleConfirm: '',
    btnOk: '',
    btnCancel: '',
    maskText: ''
  }
}

/**
 * Get language resource by language code
 * @param {string} code - language code
 * @returns {object} language resource
 */
export function getLanguage (code = CN) {
  return language[code]
}

export default language



